<template>
  <div class="rank-wrapper">
    <music-list
      :song-lists="songList"
      :name="name"
      :icon="icon"
      @pullingUp="pullingUpHandle"
      ref="musicList"
    />
  </div>
</template>

<script>
import MusicList from 'components/content/MusicList.vue';
import { createNamespacedHelpers } from 'vuex';
import { getSongDetail } from 'api/song/song';

const { mapState, mapMutations } = createNamespacedHelpers('rankDetailModule');
export default {
  components: {
    MusicList,
  },
  data() {
    return {
    };
  },
  watch: {},
  computed: {
    ...mapState([
      'songList',
      'songIdList',
      'name',
      'icon',
    ]),
  },
  methods: {
    pullingUpHandle() {
      if (this.songList.length >= this.songIdList.length) {
        return;
      }
      const index = this.songList.length - 1;
      const ids = this.songIdList.slice(index, index + 20);
      getSongDetail(ids.join(','))
        .then((res) => {
          this.setSongList([...this.songList, ...res]);
          setTimeout(() => {
            this.$refs.musicList.finishPullUp();
          }, 20);
        });
    },
    ...mapMutations([
      'setSongList',
    ]),
  },
  created() {},
  mounted() {},
};
</script>
<style lang="scss" scoped>
  @import '~assets/style/variable.scss';
  .rank-wrapper {
    position: fixed;
    top: 0px;
    left: 0px;
    right: 0px;
    bottom: 0px;
    background: $color-background;
  }
</style>
